<template>
    <div class="box box-primary">
        <div class="box-header with-border">
            <h3 class="box-title">Quản lý tin ưu tiên</h3>
            <div class="box-tools pull-right">
                <router-link :to="{name: 'user.addproductpriority'}" class="btn btn-success">Thêm tin ưu tiên</router-link>
            </div>
            <div class="row product-priority-controller">
                <div class="col-xs-6">
                    <Select2 v-model="productStatus" 
                    :options="dataStatus" 
                    @change="changeStatusProduct($event)" />
                </div>
                <div class="col-xs-6">
                    <Select2 v-model="pageSize" 
                    :options="[{id:10, text: '10sp/Trang'}, {id:20, text: '20sp/Trang'}, {id:50, text: '50sp/Trang'}]" 
                    @change="changePageSize($event)" />
                </div>
            </div>
            <div class="input-group user-product-search">
                <input v-on:keyup="search" type="text" class="form-control" placeholder="Tìm kiếm... ">
                <span class="input-group-addon" title="Tìm"><i class="fa fa-search"></i></span>
            </div>
        </div>
        <div class="box-body no-padding" id="_user-product">
            <div v-for="(item, index) in product" :key="index" class="item">
                <div class="about-service">
                    <span class="time-from">Từ: <i>{{ getTime(item.time_from).format('HH:mm:ss DD/MM/YYYY') }}</i></span> - <span class="time-to">Đến: <i>{{ getTime(item.time_to).format("HH:mm:ss DD/MM/YYYY") }}</i></span> | <i>{{ isDate(item.time_to, index).fromNow() }}</i>
                    <div>
                        Tỉnh thành hiển thị sản phẩm: {{ item.province ? (item.district ? `${item.province} - ${item.district}` : item.province) : 'Toàn quốc'}}
                    </div>
                    <div>
                        Danh mục hiển thị sản phẩm: {{ item.category_priority ? item.category_priority : 'Tất cả danh mục'}}
                    </div>
                </div>
                <div class="product">
                    <div class="img">
                        <img  :src="api + '/image/product/thumbnail/'+item.image" :alt="item.name">
                        <span class="status">
                            {{ dataStatus.find(e=>e.id == item.status).text }}
                        </span>
                    </div>
                    <div class="about">
                        <h2 class="name">{{ item.name }}</h2>
                        <div class="bottom">
                            <h3 class="category">{{ item.category_product }}</h3>
                            <h3 v-if="item.price!=0" class="price">{{ number_format(item.price,0) }} đ</h3>
                            <h3 v-if="item.price==0" class="price">Thoả thuận</h3>
                        </div>
                    </div>
                    <div class="controller">
                        <a v-if="item.status==1" v-on:click="active(index)" class="btn btn-success">Đang bật</a>
                        <a v-if="item.status==0" v-on:click="active(index)" class="btn btn-danger">Đã Tắt</a>
                    </div>
                </div>
            </div>
            <paginate v-model="page"
                :page-count="pageCount"
                :page-range="3"
                :margin-pages="2"                                           
                :click-handler="runPage"
                :prev-text="`<i class='fa fa-angle-left' aria-hidden='true'></i>`"
                :next-text="`<i class='fa fa-angle-right' aria-hidden='true'></i>`"
                :container-class="'pagination'"
                :page-class="'page-item'">
            </paginate>
        </div>
    </div>
</template>

<script>
import moment from "moment";
import {mapGetters} from "vuex";
import axios from "axios";
import Select2 from "v-select2-component";
import paginate from "vuejs-paginate";

export default {
    components:{Select2, paginate},
    data(){
        return {
            api: this.$store.state.domainApi,
            id_category: 'tat-ca-danh-muc',
            productStatus: this.$route.query.status ? this.$route.query.status : 'tat-ca-trang-thai',
            dataStatus: [
                {id:'tat-ca-trang-thai', text: 'Tất cả trạng thái'},
                {id:0, text: 'Đang tắt'},
                {id:1, text: 'Đang hoạt động'},
                {id:2, text: 'Hết hạn'}
            ],
            page:1,
            pageSize:20,
            pageCount:0,
            product:[]
        }
    },
    methods:{
        getTime(date){
            var time = new moment(date);
            return time;
        },
        isDate(date, index){
            var check = moment(date).isSameOrAfter(moment());
            if(!check) this.product[index].status = 2;
            var time = new moment(date);
            return time;
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        changeCategory(){
            this.page = 1;
            this.getData();
        },
        changePageSize(size){
            this.pageSize = size;
            this.page = 1;
            this.getData();
        },
        changeStatusProduct(){
            this.page = 1;
            this.getData();
        },
        runPage(page){
            this.page = page;
            this.getData();
        },
        search(e){
            this.page = 1;
            this.getData(e.target.value); 
        },
        getData(search=''){
            axios.get(this.api + "/auth/searchproductpriority", {
                params:{
                    id_user: this.$store.state.user.id,
                    status: this.productStatus,
                    search: search,
                    page: this.page,
                    pageSize: this.pageSize,
                }
            })
            .then(response=>{
                console.log(response);
                this.product = response.data.product;
                this.pageCount = Math.ceil(response.data.count/this.pageSize);
            })
            .catch(error=>{
                console.log(error);
            });
        },
        active(index){
            axios.put(this.api + '/auth/activeproductpriority',{
                id: this.product[index].id
            },{
                headers:{
                    Authorization: `Bearer ${this.$store.state.token}`
                }
            })
            .then(response=>{
                if(response.data.success){
                    this.flash(response.data.message, 'success', { timeout: 3000 });
                    this.product[index].status = Number(!this.product[index].status);
                }
                else this.flash(response.data.message, 'error', { timeout: 3000 });
            });           
        },
        deleteProduct(index){
            this.$swal.fire({
                title: 'Bán có muốn xoá !',
                text: "Xoá vĩnh viễn sản phẩm này",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không !',
                confirmButtonText: 'Xoá !'
            }).then((result) => {
                if (result.value) {
                    axios.delete(this.api + '/auth/deleteproduct',{
                        headers:{
                            Authorization: `Bearer ${this.$store.state.token}`
                        },
                        params:{
                            id: this.product[index].id,
                            id_group_image: this.product[index].image
                        }
                    })
                    .then(response=>{
                        console.log(response.data);
                        if(response.data.success){
                            this.flash(response.data.message, 'success', { timeout: 3000 });
                            this.product[index].status = 2;
                        }
                        else this.flash(response.data.message, 'error', { timeout: 3000 });
                    })
                }
            }); 
        }
    },
    computed:{
        ...mapGetters({user: 'getUser', categories: 'getCategorySort'})
    },
    created(){
        moment.locale("vi");
        this.getData();
    }
}
</script>

